import React from "react";

import Modal from "react-bootstrap/Modal"
import Button from "react-bootstrap/Button";
import { useNavigate, useSearchParams } from "react-router-dom";
import { playerActions, playerSelectors } from "./redux/features/player/playerSlice";
import { useAppDispatch, useAppSelector } from "./redux/store";
import { playerApi } from "./api/player.api";
import { gameApi } from "./api/game.api";

export const ConnectPage: React.FC = () => {
    const [params] = useSearchParams()
    const dispatch = useAppDispatch()
    const naviage = useNavigate()

    const playerId = params.get("playerId")
    const gameId = params.get("gameId")

    const player = useAppSelector(state => playerSelectors.selectById(state, playerId!))

    const onClose = React.useCallback(() => {
        naviage("/", { replace: true })
    }, [naviage])

    React.useEffect(() => {
        if (!playerId || !gameId) {
            onClose()
        }
        dispatch(playerActions.fetchById(playerId!))
    }, [playerId, gameId, onClose, dispatch])

    if (!player) {
        return null
    }

    const onStart = () => {
        playerApi.setCurrentPlayerId(playerId!)
        playerApi.startFetchCurrentPlayer()
        gameApi.setCurrentGameId(gameId!)
        gameApi.startFetchCurrentGame()
        onClose()
    }

    return (
        <Modal
            theme="dark"
            show={true}
            centered
            backdrop="static"
            keyboard={false}
            animation={false}
        >
            <Modal.Header>
                <Modal.Title id="contained-modal-title-vcenter">
                    Verbinding maken...
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <h4>Dag {player.name}</h4>
                <p>
                    Laten we starten, ben jij dit niet? klik dan op annuleren.
                </p>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="success" onClick={onStart}>Starten</Button>
                <Button variant="warning" onClick={onClose}>Annuleren</Button>
            </Modal.Footer>
        </Modal>
    )
}